@import '@/assets/sass/variables.scss';

.bulletins-overlay {
  width: 250px;
  max-width: 100%;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 0 3px rgba($black, 0.5), 0 3px 8px rgba($black, 0.5);
  overflow: hidden;
}
.bulletins-header {
  background: $primary;
}
.bulletins-header * {
  color: white;
}
.bulletins-footer {
  background: $grey-lighter;
}
.bulletins-overlay-danger-icon {
  position: absolute;
  top: -40px; /* iconSize / 2 */
  left: 0;
  right: 0;
  margin: auto;
  z-index: 2;
  pointer-events: none;
}
.bulletins-date {
  opacity: 0.75;
}
dt,
dd {
  display: inline;
}
dt {
  font-weight: bold;
}
.button-close {
  position: absolute;
  top: 4px;
  right: 4px;
}
