
          @import '@/assets/sass/variables.scss';
          @import '@/assets/sass/mixins.scss';
        

.features-title {
  display: flex;
  line-height: 1.4;

  &-text {
    min-width: 100px;
    border: solid 1px transparent;
    border-radius: 2px;
    padding: 2px 2em 2px 2px;
    box-decoration-break: clone;

    &:hover,
    &:focus {
      border-color: #b5b5b5;
    }
  }

  &-placeholder {
    font-style: italic;
  }

  &-content {
    font-weight: bold;
  }
}

.icon {
  margin-top: 2px;
}

.edit-icon {
  order: 1;
  opacity: 0;
  margin-left: -1.5em;
  pointer-events: none;
  transform: scale(0.75);
}

.features-title:hover .edit-icon {
  opacity: 0.75;
}

.document-icon {
  margin-right: 3px;
}

.trash-icon {
  margin-right: 3px;
}

.upload-button {
  position: relative;
}

input[type='file'] {
  opacity: 0; /* invisible but it's there! */
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  cursor: pointer;
}

.load-gpx {
  margin-top: 2rem;
}

.form-export {
  display: flex;
}

.control {
  align-items: center;

  .is-checkradio[type='radio'] + label {
    line-height: 1.2rem;
  }
}

.is-disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

@media screen and (max-width: 350px) {
  .form-export {
    display: block;
  }
}
