@import '@/assets/sass/variables.scss';

:deep(a) {
  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

:deep(h4) {
  margin-top: 1rem;
  margin-bottom: 0.25rem !important;
}

:deep(abbr) {
  text-decoration: none;
  border-bottom: dotted 2px #aaa;
}

:deep(strong),
:deep(dt) {
  font-weight: bold;
  color: inherit;
}

:deep(.yeti-subpanel + .yeti-subpanel) {
  margin-top: 2rem;
}

:dep(.content-ul) {
  list-style: disc outside;
  margin-left: 2em;
}
