@import '@/assets/sass/variables.scss';

.legend {
  position: absolute;
  z-index: 6;
  top: 1.25rem;
  right: 1.25rem;

  .legend-button {
    position: static;

    button {
      width: auto;
      padding: 0 0.5em;
    }
  }

  .legend-content {
    margin-top: 0.5rem;
    margin-left: 1.25rem;
    border-radius: 2px;
    border: 1px solid lightgray;
    padding: 0.5rem;
    background: white;
    clear: both;
  }

  .legend-color {
    vertical-align: bottom;
    display: inline-block;
    width: 21px;
    height: 21px;
    margin-right: 5px;
  }
}

.opacity {
  position: absolute;
  z-index: 5;
  top: 3.5rem;
  right: 1.25rem;

  .opacity-slider {
    font-size: 1.14em;
    margin: 1px;
    width: 1.375em;
    padding: 1rem 0;
    background: $grey-dark;
    border-radius: 2px;

    &:hover {
      background: $grey;
    }
  }

  .vue-slider {
    padding: 0 9px !important;
    height: 300px !important;
    max-height: 30vh;
  }

  .vue-slider-process {
    background: $white;
  }

  .vue-slider-rail {
    background: $black;
  }
}

@media screen and (max-width: $tablet) {
  .legend {
    top: 0.5rem;

    .legend-content {
      margin-left: 0.5rem;
    }
  }

  .opacity {
    top: 2.75rem;
  }
}
