
          @import '@/assets/sass/variables.scss';
          @import '@/assets/sass/mixins.scss';
        

.yeti-article {
  max-width: 1039px;
  margin-top: 1.75em;
}
:deep(h3) {
  margin-top: 2em;
}
:deep(p) {
  margin: 1em 0;
}
:deep(ul) {
  list-style-type: disc;
}
:deep(figure) img {
  max-width: 90%;
  max-height: 500px;
}
.yeti-article--disclaimer :deep(li) {
  margin: 2em 0;
}
