
          @import '@/assets/sass/variables.scss';
          @import '@/assets/sass/mixins.scss';
        

@import '~ol/ol.css';
@import '@/assets/sass/variables.scss';

$control-margin: 0.5em;

.ol-control-layer-switcher {
  bottom: 3em;
  left: $control-margin;
  right: $control-margin;
  max-width: 400px;
  text-decoration: none;
  background-color: white;
  border: 1px solid lightgray;
  border-radius: 2px;
  padding: 0 10px 10px 10px;
  display: flex;

  & > div {
    flex: 1;

    header {
      font-weight: bold;
      padding-top: 10px;
    }
  }
}

.ol-control-layer-switcher-button {
  bottom: $control-margin;
  left: $control-margin;
}

.ol-control-recenter-on {
  top: $control-margin;
  left: 3em;
  background: $white;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.2);

  input {
    border: none;
    padding: 0.3rem;
    border-radius: 2px;
  }
}

.ol-control-recenter-on-propositions {
  top: 35px;
  left: 3em;
  background: white;
  border-radius: 2px;
  border: 1px solid lightgray;
  padding: 5px;

  li:hover {
    background: lightgrey;
    cursor: pointer;
  }
}

.map-control-listitem {
  display: flex;
}
.map-control-listitem label {
  padding-left: 0.25rem;
}

$section-padding: 1.5rem; //TODO find this variable
$box-padding: 1.25rem; //TODO find this variable
$header-height: 34px;
$box-margin: 1.5rem; //TODO find this variable
$yeti-height: calc(
  100vh - #{$navbar-height} - #{$section-padding} - 2 *#{$box-padding} - #{$header-height} - #{$box-margin} - #{$box-margin}
);

.map-container {
  position: relative;
}
@media screen and (max-width: $tablet) {
  .map-container {
    height: $yeti-height;
    padding-left: 0;
    padding-top: 0;
    padding-bottom: 0;
  }
}

@media screen and (min-width: $tablet) {
  .yeti-content {
    .map-container {
      min-height: 100%;
    }
  }
}

.ol-control-drawing-mode {
  top: $control-margin;
  left: 18.75rem;
  padding: 0;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.2);

  .ol-control-drawing-mode-inner {
    padding: 0.15rem 0.25rem 0;
    border-radius: 4px;
    background: $white;
  }

  &.is-primary .ol-control-drawing-mode-inner {
    background: $primary;
    color: $white;
  }

  .yeti-tag {
    font-size: 0.8em;
    background: rgba(0, 0, 0, 0.25);
    padding: 0.25em;
    border-radius: 2px;
    margin-left: 0.5rem;
    vertical-align: text-top;
  }

  &.is-primary .yeti-tag {
    color: $white;
  }
}

@media screen and (max-width: $widescreen) {
  .ol-control-drawing-mode {
    top: 3rem;
    left: 3rem;
  }
}

.icon-notification {
  color: $primary;
}
