          @import '@/assets/sass/mixins.scss';
        

@import '@/assets/sass/variables.scss';

p:not(:last-child),
ul:not(:last-child),
table:not(:last-child) {
  margin-bottom: 1rem;
}

.yetiform-danger {
  width: 100%;
  table-layout: fixed;
  color: $dark;

  tr + tr {
    border-top: 1px solid $grey-lighter;
  }

  td {
    vertical-align: middle;
    height: 45px;
  }

  .multiline td {
    vertical-align: baseline;
  }

  td:first-child {
    width: 60px;
  }

  td:first-child + td {
    width: 40%;
  }

  td img {
    vertical-align: -12px;
    height: 35px;
  }
}

.yetitabs {
  margin: 0;
  padding: 0.75rem 0;
}

.yetitab {
  padding: 0;
}

.yetitab-control {
  .yetitab-label {
    display: block;
    margin: 0;
    padding: 0.25rem;
    padding-left: 2rem;
    border: 1px solid transparent;
    border-radius: 4px;

    &:hover,
    &:focus {
      border-color: $grey-lighter;
    }

    &.yetitab-label:before {
      top: 0.25rem !important;
      left: 0.25rem !important;
    }
    &.yetitab-label:after {
      top: 1em !important;
      left: 1em !important;
    }
  }

  .is-checkradio:checked + label:after {
    top: 0.25rem !important;
    left: 0.25rem !important;
  }
}

.yetitab-control--selected {
  .yetitab-label {
    color: $primary;
    border-color: $primary;

    &:hover,
    &:focus {
      border-color: $primary;
    }
  }
}

@media screen and (max-width: 490px),
  screen and (min-width: $tablet) and (max-width: 970px),
  screen and (min-width: $desktop) and (max-width: 1180px),
  screen and (min-width: $widescreen) and (max-width: 1370px),
  screen and (min-width: $fullhd) and (max-width: 1650px) {
  .yetitabs {
    flex-wrap: wrap;
  }
  .yetitab {
    flex: 0 0 50%;
  }
  .yetiform-danger {
    tr,
    td {
      display: block;
    }
    tr {
      padding: 0.5em 0;
    }
    td:first-child {
      float: left;
    }
    td:first-child ~ td {
      height: auto;
      width: calc(100% - 60px);
      margin-left: 60px;
    }
  }
}

.potential-danger-labels {
  display: flex;
  align-items: stretch;
  user-select: none;
  background: linear-gradient(
    to right,
    #bfe12b,
    #bfe12b 12%,
    #fff200 20%,
    #fff200 32%,
    #f68712 40%,
    #f68712 72%,
    #ed1c24 80%,
    #ed1c24 95%,
    #c01a2c
  );

  .potential-danger-label {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1 1 20px;
    text-align: center;
    border: 3px solid transparent;

    &[disabled] {
      cursor: not-allowed;
      pointer-events: none;
      background-color: rgba(255, 255, 255, 0.8);
      color: grey;
    }

    &[selected] {
      border-color: black;
    }
  }
}
